<template>
  <div>
    <center>
      <div class="my-5">
        <span class="myHeadingBolder">Our</span>
        <span class="myHeadingLinear">Crew</span>
      </div>
    </center>
    <v-container class="border pa-4">
      <div v-for="(i, j) in crew" :key="j">
        <center>
          <div class="myHeadingLinearAvatar">
            {{ i.title }}
          </div>
        </center>
        <div class="myTextAvatar my-5">
          <center>
            {{ i.heading }}
          </center>
        </div>
        <v-layout align-center justify-space-around row wrap>
          <v-flex v-for="(i, j) in i.members" :key="j" ma-5>
            <center>
              <v-avatar size="130" style="border: 2px solid #eb5310;">
                <v-img
                  :src="i.img ? i.img : logo"
                  :lazy-src="logo"
                  class="grey lighten-2"
                >
                  <template v-slot:placeholder>
                    <v-row
                      class="fill-height ma-0"
                      align="center"
                      justify="center"
                    >
                      <v-progress-circular
                        indeterminate
                        color="grey lighten-5"
                      ></v-progress-circular>
                    </v-row>
                  </template>
                </v-img>
              </v-avatar>
              <div class="avatarName">{{ i.name }}</div>
              <div class="myTextAvatar">{{ i.college }}</div>
              <a
                :href="i.linkId"
                style="text-decoration: none; color: blue;"
                target="_blank"
                v-if="i.linkId !== ''"
              >
                <v-btn class="blue--text" icon>
                  <v-icon size="24px">mdi-linkedin</v-icon>
                </v-btn>
              </a>
            </center>
          </v-flex>
        </v-layout>
        <hr class="my-3" />
      </div>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";
import logo from "@/assets/logos/girllogo.png";
export default {
  data() {
    return {
      crew: [],
      logo: logo,
    };
  },
  mounted() {
    document.title = "Girlscript | Contact Us";
    this.update();
  },
  methods: {
    update() {
      let url = "https://sethu-1471.github.io/girlscript_api/team.json";
      try {
        axios.get(url).then((e) => {
          console.log(e.data);
          e.data.team.forEach((e) => {
            this.crew.push(e);
          });
        });
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

